.list {
  margin: 0;
  padding: 0;
  list-style: none;
  vertical-align: top;
}

.list__item {
  display: inline-block;
}

.list__item--isactive {
  padding: 10px 11px;
  border-radius: 3px;
}

.list__item--isactive:first-child {
  margin-left: -11px;
}

.list__item--isactive:hover {
  box-shadow: inset 0 0 1px 1px rgba(0, 0, 0, 0.3);
  background-color: #092f3b;
  transition: .2s background-color ease-in;
}

.list__item--isactive:active {
  color: #ae0002;
}

.list__item--ishover {
  margin-left: 10px;
}

.list__item--ishover:first-child {
  margin-left: 0;
}

.list__item--ishover:hover {
  text-decoration: underline;
  color: #5d5d5d;
}

.list__item--ishover:active {
  color: #ae0002;
}

.list__item--mobile {
  display: block;
  color: #1e1e1e;
  z-index: 6;
}

@media only screen and (max-width: 900px) {
  .list__item--mobile {
    margin-bottom: 20px;
  }
}

.list__link {
  text-decoration: none;
  font-family: inherit;
  color: inherit;
  font-size: 18px;
  font-family: inherit;
  text-transform: uppercase;
}

@media only screen and (max-width: 800px) {
  .list__link {
    font-size: 44px;
  }
}

.mobile__menu {
  display: block;
  width: 100vw;
  position: absolute;
  top: -800px;
  z-index: 4;
  padding: 10px;
  padding-left: 20px;
  vertical-align: top;
  font-family: "Barlow Condensed", sans-serif;
}

.mobile__wraper {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.95);
  z-index: 3;
  display: block;
  position: fixed;
  overflow-y: scroll;
}

.hamburger__wraper {
  display: none;
  cursor: pointer;
  width: 40px;
  margin-top: -15px;
  padding: 20px 0;
  z-index: 5;
}

@media only screen and (max-width: 900px) {
  .hamburger__wraper {
    display: inline-block;
    position: absolute;
    right: 30px;
    bottom: 40px;
  }
}

.hamburger__item::before, .hamburger__item::after {
  content: '';
  position: relative;
}

.hamburger__item, .hamburger__item::before, .hamburger__item::after {
  display: block;
  width: 100%;
  height: 6px;
  background-color: #989898;
  border-radius: 3px;
}

.hamburger__item::before {
  top: -12px;
}

.hamburger__item::after {
  bottom: -6px;
}

.is-active {
  top: 35px;
  transition: top .3s ease-in;
}

.disabled {
  transition: all .3s ease-in;
}

.menu-open .hamburger__item,
.menu-close .hamburger__item {
  transition: background-color .3s ease-in;
}

.menu-open .hamburger__item {
  background-color: #989898;
}

.menu-close .hamburger__item {
  background-color: transparent;
}

.menu-open *,
.menu-close * {
  transition: all .3s ease-in;
}

.menu-open .hamburger__item::before {
  top: -12px;
}

.menu-close .hamburger__item::before {
  transform: rotate(-45deg);
  top: 3px;
}

.menu-open .hamburger__item::after {
  bottom: -6px;
}

.menu-close .hamburger__item::after {
  transform: rotate(45deg);
  top: -3px;
}

