.logo {
    display: inline-block; }

.navigations {
    display: inline-block; }

.head {
    display: flex;
    flex-direction: row; }

.content {
    display: flex;
    flex-direction: row;

    @include r(800) {
        flex-direction: column; } }

.news {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 15px;
    margin-bottom: 45px;

    @include r(975) {
        grid-template-columns: repeat(2, 1fr); }

    @include r(750) {
        grid-template-columns: repeat(1, 1fr); } }
