body {
    font-size: $font12;
    min-width: 320px;
    position: relative;
    font-family: Georgia, 'Times New Roman', serif;
    background-color: $background-blue;
    overflow-x: hidden;
    opacity: 1;

    @include r(800) {
        font-size: $font28; } }

.l- {
    &wrapper {
        margin: 0;
        padding: 0;
        padding-top: 35px;
        background-image: url(../img/bg.jpg);
        background-repeat: no-repeat;
        background-position: top center;
        background-size: cover;

        @include r(800) {
            padding: 0; } }

    &container {
        margin: 0 auto;
        max-width: 1000px;
        border-radius: $radius;
        background-color: $container; } }

.content {
    padding: 30px;

    &__promo {
        padding-right: 30px;

        @include r(800) {
            padding-right: 0; } } }
